@import "../Style/colors";

.sell-franchise {
  min-height: 100vh;

  &__banner {
    background-image: url("../images/Sell a Franchise.jpg");
    background-position: center;
    color: $whiteColor;
    font-size: 36px;
    font-weight: 600;
    padding: 120px;
    display: flex;
    justify-content: center;
    background-size: cover;
    align-items: center;
  }

  &__body {
    padding: 40px 80px;

    &-title {
      font-size: 24px;
      color: $primaryBlueColor;
      font-weight: 700;
    }

    &-descp {
      margin: 20px 0;
      color: $greyColor;
    }

    &-content {
      &-redirect {
        display: flex;
        flex-direction: column;
      }

      &-quest {
        display: flex;

        &-text {
          font-weight: 500;
          margin: 10px 20px 10px 0;
        }

        &-action {
          margin: 10px 20px 10px 0;

          &-radio {
            margin-right: 10px;
          }

          &-link {
            width: 300px;
            display: flex;
            justify-content: center;
            background-color: $secondaryBlueColor;
            color: $whiteColor;
            padding: 8px 15px;
            font-weight: 500;
            margin: 10px 0;
            cursor: pointer;
            text-decoration: none;
          }
        }
      }

      &-francorp {
        display: flex;
        flex-direction: column;
        padding: 0 60px;

        &-action {
          display: flex;
          flex-direction: column;
          padding: 10px;
          background-color: $secondaryBlueColorLight;
          margin-right: 20px;
          margin-top: 20px;
          height: max-content;
          

          &-text {
            margin: 10px 0;
          }

          &-button {
            display: flex;
            justify-content: center;
            background-color: $secondaryBlueColor;
            color: $whiteColor;
            width: 300px;
            padding: 8px 15px;
            font-weight: 500;
            margin: 10px 0;
            cursor: pointer;
            border: none;
          }
        }

        &-pdf {
          margin-top: 20px;
          //width: 36%;
          height: 580px;
        }
      }

      &-others {
        display: flex;

        &-action {
          display: flex;
          padding: 20px;
          background-color: $secondaryBlueColorLight;
          margin-right: 20px;
          height: max-content;

          &-text {
            margin: 10px 0;
          }

          &-button {
            display: flex;
            justify-content: center;
            background-color: $secondaryBlueColor;
            color: $whiteColor;
            width: 300px;
            font-weight: 500;
            padding: 8px 15px;
            margin-left: 20px;
            cursor: pointer;
            border: none;
          }
        }
      }
    }
  }
}

@media (max-width: 750px) {
  .sell-franchise {
    min-height: unset;

    &__banner {
      font-size: 24px;
      padding: 20px;
    }

    &__body {
      padding: 20px;

      &-content {
        &-quest {
          flex-direction: column;
        }

        &-francorp {
          padding: 0;

          &-action {
            &-button {
              padding: 8px 0;
              width: 100%;
              font-size: 16px;
            }
          }
        }
      }
    }
  }
}
